<section class="trips">
    <LayoutCenter>
        <TripComposer on:searching={() => loading = true} on:results={showResults} />

        {#if loading}
            <LoadingScim />
        {:else}
            {#each trips as trip}
                <TripResult {trip} on:selectTrip={selectTrip} />
            {/each}
        {/if}
    </LayoutCenter>
</section>

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Stores
import { availableTripsStore, getTripFromAddress } from "@/js/controllers/trips.js";

// Components
import TripResult from "@/components/Trips/TripResult.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
// import EmptyState from "@/components/EmptyState.svelte";
import TripComposer from "@/components/Trips/TripComposer.svelte";
import LoadingScim from "@/components/Misc/LoadingScim.svelte";

// Data
var trips = [];
var loading = false;

// Computed
$: fromAddress = getTripFromAddress($availableTripsStore);
$: toAddress = getTripFromAddress($availableTripsStore);

// Function
function showResults({ detail: results }) {
    loading = false;
    trips = results;
}

function selectTrip({ detail }) {
    dispatch("selectTrip", detail);
}
</script>

