<script>
import { getStepColor, getStepTextColor } from "@/js/stepUtils.js";

// Components
import InfoRow from "@/components/Misc/InfoRow.svelte";
import HeadSign from "@/components/HeadSign.svelte";
import DropDown from "@/components/DropDown.svelte";

// Props
export var step;
console.log(step);

// Data
var journey = step.journey;

// Computed
$: journeyAvailable = journey !== null;
$: cancelled = journeyAvailable && journey.cancelled;
$: backgroundColor = getStepColor(step);
$: textColor = getStepTextColor(step);
$: startTimeString = step.departureTime.timeString;
$: endTimeString = step.arrivalTime.timeString;
$: originPlatform = 1;
$: destinationPlatform = 1;
</script>

