  width: 100%;
  margin-bottom: 16px; }
  .trainImages .trainPartImage {
    max-height: 32px;
    width: 100%; }

.facilityIcon {
  margin-left: 4px; }

.exitDirection {
  text-align: center; }

.cancelled {
  color: var(--errorColor);
  text-align: center;
  font-weight: 700; }

.liveDataActive {
  color: var(--liveDataActive); }

