<div class="container">
    {#if !hasGPS}
        <p><span class="mdi mdi-crosshairs-question"></span> Locatie kan niet worden gevonden.</p>
    {/if}
    {#if !online}
        <p><span class="mdi mdi-wifi-strength-alert-outline"></span> Je bent offline. Gegevens worden niet bijgewerkt.</p>
    {/if}
    <RouteGuide route={$routeStore} />
    <Divider />
    <TripList on:selectTrip={selectTrip} />
</div>

<script>
// Imports
import { locationStore, locationIsValid, startLocation } from "@/js/locationUtils.js";
import { routeStore } from "@/js/controllers/trips.js";

// Components
import RouteGuide from "@/components/RouteGuide.svelte";
import TripList from "@/components/TripList.svelte";
import Divider from "@/components/Divider.svelte";

// Data
var online = navigator.onLine || true;

// Computed
$: hasGPS = locationIsValid($locationStore);

// Functions
function selectTrip({ detail }) {
    routeStore.set(detail);
}

window.addEventListener("online", () => online = true);
window.addEventListener("offline", () => online = false);

startLocation();
</script>

<style lang="scss">p {
  width: calc(95% - 32px);
  margin: 16px auto;
  text-align: center;
  font-weight: 700;
  color: var(--errorColor); }

