  padding: 8px 0;
  margin: 8px auto;
  box-sizing: border-box; }
  .trip.open {
    padding: 24px 16px;
    border-radius: 10px;
    box-shadow: var(--defaultBoxShadow); }
  .trip .buttons {
    width: 100%;
    margin: 8px 0;
    display: inline-block; }
    .trip .buttons > :global(button) {
      width: 50%;
      float: left; }
  .trip .tripSteps :global(.routeStep) {
    width: calc(100% - 16px); }

.tripIcons {
  font-size: 18px; }

